.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	&__logo {
		 width: 250px;
		 img {
			  width: 100%;
		 }
	}
	&__official {
		color: #ffffff;
		font-size: 12px;
		font-weight: 500;
		text-align: center;
		span {
			display: block;
			font-size: 18px;
		}
	}
	&__phone{
		display: block;
		font-size: 18px;
		font-weight: 700;
		color: #ffffff;
		text-decoration: none;
		&:hover {
			text-decoration: none;
			color: #ffffff;
		}
	}
	&__contacts {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}
}